<template>
  <div>
    <div class="il-terbaru">
      <h1 class="content-header">{{ this.$route.name }}</h1>
      <div class="content">
        <base-header class="pb-6" type="primary">
          <div class="row">
            <div class="col-lg-12 py-2 col-5 text-right">
              <el-select
                v-show="$store.getters.isAdmin"
                v-model="selects.unitId"
                size="mini"
                placeholder="Pilih Unit Kerja"
                @change="getData(selects.unitId)"
              >
                <el-option
                  v-for="option in cabangShift(cabang)"
                  :key="option.label"
                  :label="option.label"
                  :value="option.value"
                ></el-option>
              </el-select>
              <base-button @click="toForm" class="ml-2" size="sm" type="neutral"
                >Ubah</base-button
              >
            </div>
          </div>
        </base-header>
        <div v-if="isTable" class="container-fluid mt--6">
          <div>
            <card
              class="no-border-card"
              body-classes="p-0"
              footer-classes="p-2"
            >
              <div class="card-header">
                <h2 class="m-0">Izin Lingkungan {{ dataLast.IL_NAME }}</h2>
              </div>
              <div class="card-body">
                <div class="row m-0">
                  <div
                    class="border border-primary overflow-hidden position-relative w-100"
                    style="height: 512px"
                  >
                    <div class="d-flex justify-content-center w-100">
                      <img
                        v-if="allPeta.petaZonasi"
                        class="img-fluid"
                        :src="dataLast.IL_MAPZONASI_LINK"
                        style="height: 480px"
                      />
                      <img
                        v-if="allPeta.petaBatas"
                        class="img-fluid"
                        :src="dataLast.IL_MAPBASWIL_LINK"
                        style="height: 480px"
                      />
                      <img
                        v-if="allPeta.petaRKL"
                        class="img-fluid"
                        :src="dataLast.IL_MAPRKL_LINK"
                        style="height: 480px"
                      />
                      <img
                        v-if="allPeta.petaRPL"
                        class="img-fluid"
                        :src="dataLast.IL_MAPRPL_LINK"
                        style="height: 480px"
                      />
                    </div>
                    <div class="select-map w-100">
                      <button
                        @click="viewPetaZonasi"
                        class="btn btn-primary col-3 m-0 p-1 rounded-0 text-white"
                      >
                        Peta Zonasi
                      </button>
                      <button
                        @click="viewPetaBatas"
                        class="btn btn-primary col-3 m-0 p-1 rounded-0 text-white"
                      >
                        Peta Batas
                      </button>
                      <button
                        @click="viewPetaRKL"
                        class="btn btn-primary col-3 m-0 p-1 rounded-0 text-white"
                      >
                        Peta RKL
                      </button>
                      <button
                        @click="viewPetaRPL"
                        class="btn btn-primary col-3 m-0 p-1 rounded-0 text-white"
                      >
                        Peta RPL
                      </button>
                    </div>
                  </div>
                  <div class="mt-4 w-100">
                    <span>Tanggal: {{ changeToDate(dataLast.IL_DATE) }}</span>
                  </div>
                  <div class="mt-4 w-100">
                    <span
                      >Nomor SK Pengesahan: {{ dataLast.IL_NOMORIZIN }}</span
                    >
                  </div>
                  <div class="mt-4 w-100">
                    <span v-html="dataLast.IL_CONTENT"></span>
                  </div>
                </div>
              </div>
            </card>
          </div>
        </div>
        <div v-if="isForm" style="padding: 30px 30px 0;">
          <div class="card border-0">
            <div class="card-header">
              <h2>Ubah Form Izin Lingkungan</h2>
            </div>
            <div class="card-body">
              <base-input
                v-show="$store.getters.isAdmin"
                label="Unit Kerja"
                placeholder="Pilih Unit Kerja"
                class="form-group"
              >
                <select
                  placeholder="Pilih Unit Kerja"
                  v-model="formIzinLingkungan.uk_id"
                  class="form-control"
                >
                  <option
                    v-for="option in cabang"
                    :key="option.label"
                    :label="option.label"
                    :value="option.value"
                  ></option>
                </select>
              </base-input>
              <base-input
                v-model="formIzinLingkungan.il_name"
                label="Nama Izin Lingkungan"
                placeholder="Nama Izin Lingkungan"
                class="form-group"
              ></base-input>
              <base-input label="Tanggal Pengesahan" class="form-group">
                <flat-picker
                  slot-scope="{ focus, blur }"
                  @on-open="focus"
                  @on-close="blur"
                  :config="{
                    allowInput: true,
                    dateFormat: 'U',
                    altFormat: 'd/m/Y',
                    altInput: true,
                  }"
                  class="form-control datepicker"
                  v-model="formIzinLingkungan.date"
                ></flat-picker>
              </base-input>
              <base-input
                v-model="formIzinLingkungan.il_nomorizin"
                type="string"
                label="Nomor SK Pengesahan"
                placeholder="80319203910293"
                class="form-group"
              ></base-input>
              <h5>Peta Zonasi</h5>

              <form
                class="mb-3"
                method="post"
                @change="insertName('zonasi')"
                action="#"
                enctype="multipart/form-data"
              >
                <div class="input-group">
                  <div class="custom-file">
                    <input
                      type="file"
                      class="custom-file-input"
                      ref="fileZonasi"
                      id="inputGroupFile01"
                    />
                    <label
                      v-if="!petaZonasiName"
                      class="custom-file-label"
                      for="inputGroupFile01"
                      >Click to browse file</label
                    >
                    <label
                      v-if="petaZonasiName"
                      class="custom-file-label"
                      for="inputGroupFile01"
                      >{{ petaZonasiName }}</label
                    >
                  </div>
                  <div class="input-group-append">
                    <button
                      class="btn base-button btn-primary"
                      type="button"
                      @click="submitFiles('zonasi')"
                    >
                      Upload
                    </button>
                    <button
                      v-if="formIzinLingkungan.il_mapzonasi_link"
                      class="btn base-button btn-danger"
                      type="button"
                      @click="clearImg('zonasi')"
                    >
                      Hapus
                    </button>
                  </div>
                </div>
                <img
                  class="p-3 mx-auto img-fluid mb-3 d-block"
                  v-if="formIzinLingkungan.il_mapzonasi_link"
                  :src="formIzinLingkungan.il_mapzonasi_link"
                />
              </form>

              <h5>Peta Batas Wilayah Studi</h5>
              <form
                class="mb-3"
                method="post"
                @change="insertName('batas')"
                action="#"
                enctype="multipart/form-data"
              >
                <div class="input-group">
                  <div class="custom-file">
                    <input
                      type="file"
                      class="custom-file-input"
                      ref="fileBatas"
                      id="inputGroupFile02"
                    />
                    <label
                      v-if="!petaBatasName"
                      class="custom-file-label"
                      for="inputGroupFile02"
                      >Click to browse file</label
                    >
                    <label
                      v-if="petaBatasName"
                      class="custom-file-label"
                      for="inputGroupFile02"
                      >{{ petaBatasName }}</label
                    >
                  </div>
                  <div class="input-group-append">
                    <button
                      class="btn base-button btn-primary"
                      type="button"
                      @click="submitFiles('batas')"
                    >
                      Upload
                    </button>
                    <button
                      v-if="formIzinLingkungan.il_mapbaswil_link"
                      class="btn base-button btn-danger"
                      type="button"
                      @click="clearImg('batas')"
                    >
                      Hapus
                    </button>
                  </div>
                </div>
                <img
                  class="p-3 mx-auto img-fluid mb-3 d-block"
                  v-if="formIzinLingkungan.il_mapbaswil_link"
                  :src="formIzinLingkungan.il_mapbaswil_link"
                />
              </form>

              <h5>Peta RKL</h5>

              <form
                class="mb-3"
                method="post"
                @change="insertName('RKL')"
                action="#"
                enctype="multipart/form-data"
              >
                <div class="input-group">
                  <div class="custom-file">
                    <input
                      type="file"
                      class="custom-file-input"
                      ref="fileRKL"
                      id="inputGroupFile03"
                    />
                    <label
                      v-if="!petaRKLName"
                      class="custom-file-label"
                      for="inputGroupFile03"
                      >Click to browse file</label
                    >
                    <label
                      v-if="petaRKLName"
                      class="custom-file-label"
                      for="inputGroupFile03"
                      >{{ petaRKLName }}</label
                    >
                  </div>
                  <div class="input-group-append">
                    <button
                      class="btn base-button btn-primary"
                      type="button"
                      @click="submitFiles('RKL')"
                    >
                      Upload
                    </button>
                    <button
                      v-if="formIzinLingkungan.il_maprkl_link"
                      class="btn base-button btn-danger"
                      type="button"
                      @click="clearImg('RKL')"
                    >
                      Hapus
                    </button>
                  </div>
                </div>
                <img
                  class="p-3 mx-auto img-fluid mb-3 d-block"
                  v-if="formIzinLingkungan.il_maprkl_link"
                  :src="formIzinLingkungan.il_maprkl_link"
                />
              </form>

              <h5>Peta RPL</h5>

              <form
                class="mb-3"
                method="post"
                @change="insertName('RPL')"
                action="#"
                enctype="multipart/form-data"
              >
                <div class="input-group">
                  <div class="custom-file">
                    <input
                      type="file"
                      class="custom-file-input"
                      ref="fileRPL"
                      id="inputGroupFile04"
                    />
                    <label
                      v-if="!petaRPLName"
                      class="custom-file-label"
                      for="inputGroupFile04"
                      >Click to browse file</label
                    >
                    <label
                      v-if="petaRPLName"
                      class="custom-file-label"
                      for="inputGroupFile04"
                      >{{ petaRPLName }}</label
                    >
                  </div>
                  <div class="input-group-append">
                    <button
                      class="btn base-button btn-primary"
                      type="button"
                      @click="submitFiles('RPL')"
                    >
                      Upload
                    </button>
                    <button
                      v-if="formIzinLingkungan.il_maprpl_link"
                      class="btn base-button btn-danger"
                      type="button"
                      @click="clearImg('RPL')"
                    >
                      Hapus
                    </button>
                  </div>
                </div>
                <img
                  class="p-3 mx-auto img-fluid mb-3 d-block"
                  v-if="formIzinLingkungan.il_maprpl_link"
                  :src="formIzinLingkungan.il_maprpl_link"
                />
              </form>

              <h5>Konten</h5>
              <form>
                <html-editor
                  ref="formHTML"
                  v-model="formIzinLingkungan.il_content"
                ></html-editor>
                <!-- <base-button size="sm" type="neutral" @click="saveHTML">Simpan</base-button> -->
              </form>
              <div class="col-lg-12 py-2 col-5 text-right">
                <base-button size="sm" type="neutral" @click="hideForm"
                  >Cancel</base-button
                >
                <base-button size="sm" type="neutral" @click="submitForm"
                  >Simpan</base-button
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "sweetalert2/dist/sweetalert2.css";
import HtmlEditor from "@/components/Inputs/HtmlEditor";
import DropzoneFileUpload from "@/components/Inputs/DropzoneFileUpload";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import { Table, TableColumn, Select, Option } from "element-ui";
import { BasePagination, BaseInput } from "@/components";
import clientPaginationMixin from "@/components/clientPaginationMixin";
import swal from "sweetalert2";
import users from "./users2";
const axios = require("axios");
import moment from "moment";
import cabang from "../../cabang";
import { computeVisibleDayRange } from "@fullcalendar/core";

export default {
  components: {
    HtmlEditor,
    BasePagination,
    BaseInput,
    flatPicker,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn,
  },
  created() {
    this.getData(this.$store.getters.getUKId);
  },
  data() {
    return {
      il_id: 0,
      petaRPLName: "",
      petaRKLName: "",
      petaBatasName: "",
      petaZonasiName: "",
      formIzinLingkungan: {
        uk_id: 0,
        il_name: "",
        il_content: "",
        il_mapzonasi_link: "",
        il_mapbaswil_link: "",
        il_maprkl_link: "",
        il_maprpl_link: "",
        il_nomorizin: "",
        date: 0,
      },
      isForm: false,
      isTable: true,
      allPeta: {
        petaZonasi: true,
        petaBatas: false,
        petaRKL: false,
        petaRPL: false,
      },
      cabang,
      dataLast: {},
      selects: {
        unitId: null,
      },
    };
  },
  computed: {},
  methods: {
    cabangShift(data) {
      let tempCabang = [];
      for (let i = 1; i < data.length; i++) {
        tempCabang.push(data[i]);
      }
      return tempCabang;
    },
    changeToDate(date) {
      return moment.unix(date).format("DD/MM/YYYY");
    },
    submitFiles(data) {
      let formData = new FormData();

      if (data === "zonasi") {
        formData.append("picture", this.$refs.fileZonasi.files[0]);
      }
      if (data === "batas") {
        formData.append("picture", this.$refs.fileBatas.files[0]);
      }
      if (data === "RKL") {
        formData.append("picture", this.$refs.fileRKL.files[0]);
      }
      if (data === "RPL") {
        formData.append("picture", this.$refs.fileRPL.files[0]);
      }

      axios
        .post("http://45.127.133.75:3000/api/iems/img", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((res) => {
          if (data === "zonasi") {
            this.formIzinLingkungan.il_mapzonasi_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "batas") {
            this.formIzinLingkungan.il_mapbaswil_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "RKL") {
            this.formIzinLingkungan.il_maprkl_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
          if (data === "RPL") {
            this.formIzinLingkungan.il_maprpl_link =
              "http://45.127.133.75:3000" + res.data.path;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    insertName(data) {
      if (data === "zonasi") {
        this.petaZonasiName = this.$refs.fileZonasi.files[0].name;
      }
      if (data === "batas") {
        this.petaBatasName = this.$refs.fileBatas.files[0].name;
      }
      if (data === "RKL") {
        this.petaRKLName = this.$refs.fileRKL.files[0].name;
      }
      if (data === "RPL") {
        this.petaRPLName = this.$refs.fileRPL.files[0].name;
      }
    },
    clearImg(data) {
      if (data === "zonasi") {
        this.petaZonasiName = null;
        this.formIzinLingkungan.il_mapzonasi_link = null;
      }
      if (data === "batas") {
        this.petaBatasName = null;
        this.formIzinLingkungan.il_mapbaswil_link = null;
      }
      if (data === "RKL") {
        this.petaRKLName = null;
        this.formIzinLingkungan.il_maprkl_link = null;
      }
      if (data === "RPL") {
        this.petaRPLName = null;
        this.formIzinLingkungan.il_maprpl_link = null;
      }
    },
    submitForm() {
      let formData = this.formIzinLingkungan;
      formData.il_content = this.$refs.formHTML.content;
      formData.il_date = Number(formData.il_date);
      axios
        .put("http://45.127.133.75:3000/api/iems/il/" + this.il_id, formData, {
          headers: { "Content-Type": "application/json" },
        })
        .then((res) => {
          swal.fire({
            title: `Success`,
            text: "Form berhasil tersimpan!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-success",
            icon: "success",
          });
        })
        .catch((err) => {
          console.log(err);
          swal.fire({
            title: `Warning`,
            text: "Form tidak tersimpan!!!",
            buttonsStyling: false,
            confirmButtonClass: "btn btn-warning",
            icon: "warning",
          });
        });
    },
    hideForm() {
      this.isForm = false;
      this.isTable = true;
    },
    toForm() {
      // this.formIzinLingkungan.date = this.formIzinLingkungan.date.toString();
      this.isForm = true;
      this.isTable = false;
    },
    getData(data) {
      axios
        .get("http://45.127.133.75:3000/api/iems/il-latest/" + data)
        .then((res) => {
          console.log(res.data);
          this.il_id = res.data[0].IL_ID;
          this.dataLast = res.data[0];
          this.formIzinLingkungan.uk_id = res.data[0].UNITKERJA_ID;
          this.formIzinLingkungan.il_name = res.data[0].IL_NAME;
          this.formIzinLingkungan.il_nomorizin = res.data[0].IL_NOMORIZIN;
          this.formIzinLingkungan.date = res.data[0].IL_DATE.toString();
          this.formIzinLingkungan.il_mapzonasi_link =
            res.data[0].IL_MAPZONASI_LINK;
          this.formIzinLingkungan.il_mapbaswil_link =
            res.data[0].IL_MAPBASWIL_LINK;
          this.formIzinLingkungan.il_maprkl_link = res.data[0].IL_MAPRKL_LINK;
          this.formIzinLingkungan.il_maprpl_link = res.data[0].IL_MAPRPL_LINK;
          this.formIzinLingkungan.il_content = res.data[0].IL_CONTENT;
        });
    },
    setFalsePeta() {
      this.allPeta.petaZonasi = false;
      this.allPeta.petaBatas = false;
      this.allPeta.petaRKL = false;
      this.allPeta.petaRPL = false;
    },
    viewPetaZonasi() {
      this.setFalsePeta();
      this.allPeta.petaZonasi = true;
    },
    viewPetaBatas() {
      this.setFalsePeta();
      this.allPeta.petaBatas = true;
    },
    viewPetaRKL() {
      this.setFalsePeta();
      this.allPeta.petaRKL = true;
    },
    viewPetaRPL() {
      this.setFalsePeta();
      this.allPeta.petaRPL = true;
    },
  },
};
</script>

<style lang="scss">
.il-terbaru {
  .select-map {
    .btn-primary {
      &:hover {
        -webkit-transform: unset;
        transform: unset;
      }
      &:active {
        background-color: white;
        color: #525f7f !important;
        border-color: transparent;
      }
    }
  }
  .el-select--mini {
    .el-input--mini {
      input {
        height: 28px;
      }
    }
  }
}
</style>
